.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0; }

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto; }

body {
  background-color: #292929; }

.App {
  overflow: hidden; }
  .App .row {
    height: 100%;
    width: 100%;
    background-color: #292929; }
    .App .row .sidebar {
      position: fixed;
      width: 60px;
      height: 100vh;
      background: #181818;
      z-index: 1000; }
    .App .row .content {
      width: 100%;
      height: 100%;
      background-color: #292929;
      padding-left: 60px; }
      @media only screen and (max-width: 900px) {
        .App .row .content {
          padding-left: 30px; } }

